.price-section{
    padding: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
    margin:10px;
    border-radius: 10px;
}
.product-card{
    height:200px;
    display:flex;
    width:100%;
    margin:10px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
    border-radius: 10px;

}
.product-card-title{
    padding:30px;
}
.product-card-image{
    height:100%;
    width:300px;
    object-fit: contain;
}
.amount-title{
    font-size:20px;
    margin-bottom:0px;
}
